<template>
  <div class="column">
    <div class="tile is-child notification">
      <figure class="image" @click="getToCard()">
        <img :src="img" alt="Oops" />
      </figure>
      <div class="tile notification is-child is-warning">
        <p class="title is-5 notification is-success" @click="getToCard()">
          {{ title }}
        </p>
        <p class="content">{{ subtitle }}</p>
        <p class="has-text-right">Категорія:</p>
        <p class="subtitle is-6">{{ category }}</p>
        <p class="subtitle is-6">Ціна: {{ showPrice }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProductCard",
  props: {
    title: {
      type: String,
      default: "",
    },
    subtitle: {
      type: String,
      default: "",
    },
    id: {
      type: [Number, String],
      default: null,
    },
    img: {
      type: String,
      default: "",
    },
    category: {
      type: String,
      default: "",
    },
    price: {
      type: [Number, String],
      default: "not available",
    },
  },
  computed: {
    showPrice() {
      let showPrice = this.price.toString();
      return showPrice[0] + " " + showPrice.slice(1);
    },
  },
  methods: {
    getToCard() {
      this.$router.push({
        name: "card",
        params: { product_id: this.id },
      });
    },
  },
};
</script>

<style lang="css" scoped>
.image:hover,
.title:hover {
  cursor: pointer;
}
</style>